//
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Search from '@/pages/Search'
import Register from '@/pages/Register'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
//
export default [
    {
        name:'home',
        path:'/home',
        component:Home,
        meta:{show:true}
    },
    {
        path:'/login',
        name:'login',
        component:Login,
        meta:{show:false}
    },
    {
        name:'search',
        path:'/search/:keyword?',     //params
        component:Search,
        meta:{show:true},
        props:true                      //props,
    },
    {
        path:'/register',
        component:Register,
        meta:{show:false}
    },
    {
        name:'detail',
        path:'/detail/:skuId?',
        component:Detail,
        meta:{show:false}
    },
    {
        name:'addCartSuccess',
        path:'/AddCartSuccess/:skuID/:skuNum',
        component:AddCartSuccess,
        meta:{show:true},
    },
    ///
    {
        path:'*',
        redirect:"/home"
    }
]